body header{
    position: absolute;
    width: 100%;
    z-index: 999;

    ul{
        list-style: none;
        padding: 0;

        li {
            float: left;
            margin: 0 15px;
            
            a {
                i {
                    &::before{
                        font-size: 35px;
                    }
                }
            }
        }
    }
}